  methods: {
    formatterMethod: function(val) {
      if (val === "get") {
        return "GET";
      }
      if (val === "post") {
        return "POST";
      }
    },
    formatterContentType: function(val) {
      // Content-Type
      if (val === "application/json;charset=UTF-8") {
        return "application/json";
      }
      if (val === "application/x-www-form-urlencoded;charset=UTF-8") {
        return "x-www-form-urlencoded";
      }
      return val;
    },
    // 初始化数据
    initData: function() {
      this.addFormNameVisible = false;
      this.bodyFormDataVisible = false;
      this.bodyXFormDataVisible = false;
      this.jsonValueVisible = false;
      this.sendLoading = false;
      this.addFormLoading = false;
      this.jsonDataVisible = false;

      // this.jsonData = "{}";
      this.jsonData = "";

      this.contentType = "none";
      this.responseBody = "";
      this.dialogTitle = "";
      this.dialogDesc = "";

      this.addForm = {};

      this.bodyFormData = [
        {
          name: "",
          value: "",
          desc: ""
        }
      ];
      (this.SBodyFormData = []),
