      this.loadHttpClientList();
    },
    // #Send
    onSubmitSend: function() {
      this.responseBody = "";
      // this.jsonDataVisible = false;

      let reqData = this.getHttpRequestData();

      if (!reqData.method) {
        this.$message({
          message: "警告：请求方法不能为空哦～",
          type: "warning"
        });

        return false;
      }

      if (!reqData.url) {
        this.$message({
          message: "警告：HTTP请求地址不能为空哦～",
          type: "warning"
        });
        return false;
      }

      console.log(reqData);
      // return;

      this.sendLoading = true;
      this.jsonDataVisible = true;

      httpclientRequest(reqData).then(res => {
        this.$message({
          type: "success",
          message: "请求完成"
        });

        this.sendLoading = false;
        // console.log(res);

        try {
          this.jsonData = res;

          this.respDataType = "json";
        } catch (error) {
          console.log(error);
          this.jsonDataVisible = false;
          this.responseBody = res;
          this.respDataType = "string";
        }
      });
    },
    // 提交保存
    onSubmitSave: function() {
      let httpRequestData = this.getHttpRequestData();
      // console.log(httpRequestData);
      // return;

      if (!httpRequestData.method) {
        this.$message({
          message: "警告：请求方法不能为空哦～",
          type: "warning"
        });
        return false;
      }

      if (!httpRequestData.url) {
        this.$message({
          message: "警告：请求地址不能为空哦～",
          type: "warning"
        });
        return false;
      }

      if (!httpRequestData.name) {
        this.$message({
          message: "警告：HTTP请求名称不能为空哦～",
          type: "warning"
        });
        this.activeRequestName = "setting";
        this.nameError = "请求方法不能为空";
        return false;
      }

      let reqData = {
        name: httpRequestData.name,
        method: httpRequestData.method,
        contentType: httpRequestData.contentType,
        headers: httpRequestData.headerList,
        formData: httpRequestData.formDataList,
        requestConfig: httpRequestData,
        tags: this.dynamicTags
        // respFormat: httpRequestData.respFormat,
        // respDataType: httpRequestData.respDataType,
        // charset: httpRequestData.charset,
        // httpType: httpRequestData.httpType,
        // dispose: httpRequestData.dispose,
        // parentId: httpRequestData.parentId,
        // level: httpRequestData.level
      };

      console.log(reqData);
      // return;

      this.saveLoading = true;
      httpclientSave(reqData).then(res => {
        this.saveLoading = false;

        console.log(res);

        this.$notify({
          title: "系统提示",
          message: res.msg,
          type: res.code == 200 ? "success" : "error"
        });
      });
    },
    // #RD
    getHttpRequestData: function() {
      let reqData = {
        name: this.addForm.name,
        url: this.addForm.url,
        method: this.addForm.method,
        formData: {},
        headers: {},
        contentType: "",
        respFormat: this.respFormat,
        respDataType: this.respDataType,
        charset: this.charset,
        httpType: this.httpType,
        dispose: this.dispose === "true" ? true : false,
        parentId: this.parentId,
        level: this.addForm.level
      };

      const selectHeadersData = this.$refs.headersDataTable.selection;
      let headersLen = selectHeadersData.length;

      if (headersLen > 0) {
        let headerList = [];

        let headers = {};

        selectHeadersData.forEach(row => {
          headers[row.name] = row.value;

          headerList.push({
            name: row.name,
            value: row.value,
            desc: row.desc
          });
        });

        reqData["headers"] = headers;
        reqData["headerList"] = headerList;
      }

      let s1 = "application/x-www-form-urlencoded;charset=UTF-8";
      if (this.contentType === "form-data") {
        const selectFormData = this.$refs.bodyFormDataTable.selection;
        let formDataLen = selectFormData.length;
        if (formDataLen > 0) {
          let formDataList = [];

          let formData = {};

          selectFormData.forEach(row => {
            formData[row.name] = row.value;

            formDataList.push({
              name: row.name,
              value: row.value,
              desc: row.desc
            });
          });

          reqData["formData"] = formData;
          reqData["formDataList"] = formDataList;
        }
      } else if (this.contentType === s1) {
        const selectXFormData = this.$refs.bodyXFormDataTable.selection;
        let formDataLen = selectXFormData.length;
        if (formDataLen > 0) {
          let formDataList = [];

          let formData = {};
          selectXFormData.forEach(row => {
            formData[row.name] = row.value;

            formDataList.push({
              name: row.name,
              value: row.value,
              desc: row.desc
            });
          });

          reqData["formData"] = formData;
          reqData["formDataList"] = formDataList;
        }
      } else if (this.contentType === "application/json;charset=UTF-8") {
        try {
          let obj = JSON.parse(this.bodyJson);
          reqData["body"] = JSON.stringify(obj);
        } catch (error) {
          console.log("格式转换失败");
          reqData["body"] = "{}";
        }
      }

      reqData["contentType"] = this.contentType;

      return reqData;
    },
    // 打开新增http请求
    handleAdd: function() {
      this.dialogTitle = "新增";
      this.addFormVisible = true;
      this.addFormNameVisible = false;

      this.activeRequestName = "body";

      this.httpType = "request";
      this.respDataType = "json";
      this.respFormat = "api";
      this.dispose = "false";
      this.bodyJson = "";

      this.dynamicTags = [];

      httpclientSelect({}).then(res => {
        // console.log(res);
        if (res.code === 200) {
          this.httpClientObject = res.data;
        }
      });
    },
    handleEdit: function(index, row) {
      console.log(row);

      this.dialogTitle = "编辑";
      this.addFormVisible = true;
      this.addFormNameVisible = true;

      httpclientSelect({ host: row.host, db: row.db, qt: "1" }).then(res => {
        // console.log(res);
        if (res.code === 200) {
          this.httpClientObject = res.data;
        }
      });

      let requestConfig = row.requestConfig;

      this.addForm = {
        name: row.name,
        method: row.method,
        url: requestConfig.url,
        level: requestConfig.level
      };

      this.httpType = requestConfig.httpType;
      this.charset = requestConfig.charset;
      this.respDataType = requestConfig.respDataType;
      this.respFormat = requestConfig.respFormat;
      this.contentType = requestConfig.contentType;
      this.parentId = requestConfig.parentId;
      this.dispose = requestConfig.dispose === true ? "true" : "false";

      this.dynamicTags = row.tags == null ? [] : row.tags;

      // 展开Body
      this.activeRequestName = "body";

      if (row.contentType === "application/json;charset=UTF-8") {
        this.addForm.contentType = "3";
        this.jsonValueVisible = true;
        this.bodyJson = requestConfig.body;
      }

      let s1 = "application/x-www-form-urlencoded;charset=UTF-8";
      if (row.contentType === s1) {
        this.addForm.contentType = "2";
        this.bodyXFormDataVisible = true;

        this.bodyXFormData = row.formData == null ? [{}] : row.formData;

        if (this.bodyXFormData.length > 1) {
          this.bodyXFormData.push({});
        }

        this.$nextTick(() => {
          this.bodyXFormData.forEach(row => {
            for (const key in row) {
              if (key) {
                this.$refs.bodyXFormDataTable.toggleRowSelection(row, true);
              }
            }
          });
        });
      }

      if (row.contentType === "form-data") {
        this.addForm.contentType = "1";
        this.bodyFormDataVisible = true;

        this.bodyFormData = row.formData == null ? [{}] : row.formData;
        if (this.bodyFormData.length > 1) {
          this.bodyFormData.push({});
        }

        this.$nextTick(() => {
          this.bodyFormData.forEach(row => {
            for (const key in row) {
              if (key) {
                this.$refs.bodyFormDataTable.toggleRowSelection(row, true);
              }
            }
          });
        });
      }

      if (row.contentType === "none") {
        this.addForm.contentType = "0";
      }

      // this.headersData = row.headers;
      // this.headersData.push({});

      this.headersData = row.headers == null ? [{}] : row.headers;
      if (this.headersData.length > 1) {
        this.headersData.push({});
      }

      this.$nextTick(() => {
        this.headersData.forEach(row => {
          // console.log(row); // 这里仅仅加了个打印操作
          for (const key in row) {
            if (key) {
              this.$refs.headersDataTable.toggleRowSelection(row, true);
            }
          }
        });
      });
    },
    handleDelete: function(index, row) {
      console.log(row);
      this.$confirm("确定要删除此记录吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          httpclientDelete(row).then(res => {
            console.log(res);

            this.$notify({
              title: "系统提示",
              message: res.msg,
              type: res.code == 200 ? "success" : "error"
            });

            if (res.code === 200) {
              this.loadHttpClientList();
            }
          });
        })
        .catch(() => {});
    },
    // 关闭新增 or 编辑窗口
    cancelAdd: function() {
      this.addFormVisible = true;

      this.$confirm("确定要关闭窗口吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.addFormVisible = false;
          this.sendLoading = false;
          this.dynamicTags = [];

          this.initData();
          // if (this.dialogTitle === "编辑") {
          this.loadHttpClientList();
          // }
        })
        .catch(() => {
          // this.$message({
          //   type: "info",
          //   message: "已取消删除"
          // });
        });
    },
    handleChangeContentType: function() {
      // alert(val);
      // console.log(this.addForm.contentType);

      let val = this.addForm.contentType;

      if (val === "0") {
        this.contentType = "";

        this.bodyFormDataVisible = false;
        this.bodyXFormDataVisible = false;
        this.jsonValueVisible = false;
      } else if (val == 1) {
        this.bodyXFormDataVisible = false;
        this.jsonValueVisible = false;

        this.contentType = "form-data";
        this.bodyFormDataVisible = true;
      } else if (val === "2") {
        this.bodyFormDataVisible = false;
        this.jsonValueVisible = false;

        //application/x-www-form-urlencoded;charset=UTF-8
        this.contentType = "application/x-www-form-urlencoded;charset=UTF-8";
        this.bodyXFormDataVisible = true;
      } else if (val == "3") {
        // application/json;charset=UTF-8

        this.bodyFormDataVisible = false;
        this.bodyXFormDataVisible = false;

        this.contentType = "application/json;charset=UTF-8";
        this.jsonValueVisible = true;
      }
    },
    // 触发表格的input控件时 调用
    handleValueChange: function(index, row, type) {
      let data = {
        name: "",
        value: "",
        desc: ""
      };
      // console.log("index");
      // console.log(index);
      // console.log("row");
      // console.log(row);
      // console.log("type");
      // console.log(type);

      // if (type === "paramsData") {
      //   let len = this.paramsData.length;
      //   console.log("paramsData len=" + len);
      //   if (index + 1 == len) {
      //     this.$refs.paramsDataTable.toggleRowSelection(row);
      //     let data = {
      //       name: "",
      //       value: "",
      //       desc: ""
      //     };
      //     this.paramsData.push(data);
      //   }
      // }

      if (type === "headersData") {
        let len = this.headersData.length;
        // console.log("headersData len=" + len);

        if (index + 1 == len) {
          this.$refs.headersDataTable.toggleRowSelection(row, true);
          this.headersData.push(data);
        }
      }

      if (type === "bodyFormData") {
        let len = this.bodyFormData.length;
        // console.log("bodyFormData len=" + len);

        if (index + 1 == len) {
          this.$refs.bodyFormDataTable.toggleRowSelection(row, true);
          this.bodyFormData.push(data);
        }
      }

      if (type === "bodyXFormData") {
        let len = this.bodyXFormData.length;
        // console.log("bodyXFormData len=" + len);

        if (index + 1 == len) {
          this.$refs.bodyXFormDataTable.toggleRowSelection(row, true);
          this.bodyXFormData.push(data);
        }
      }
    },
    // 加载列表
    loadHttpClientList: function() {
      let reqData = {
        // host: "47.95.12.18",
        // db: "0",
        keyword: this.queryForm.keyword
      };

      this.httpClientDataLoading = true;
      httpclientList(reqData).then(res => {
        this.httpClientDataLoading = false;
        console.log(res);
        if (res.code === 200) {
          this.httpClientData = res.data;
        }
      });
    },
    handleSelectionChange1: function(val) {
      console.log(val);
      this.SBodyFormData = val;
    },
    handleSelectionChange2: function(val) {
      console.log(val);
      this.SBodyXFormData = val;
    },
    handleCurrentChange2: function(val) {
      console.log(val);
      this.currentRow2 = val;
    },
    handleRequestClick: function() {},
    handleResponseClick: function() {},
    handleRespFormat: function(val) {
      // alert(val);
      this.respFormat = val;
      this.onSubmitSend();
    },
    handleRespDataType: function() {
      this.respDataType = val;
    },
    handleHttpClientChange: function(val) {
      this.parentId = val;
    },
    handleDeleteRow: function(index, row, name) {
      console.log(index);
      console.log(row);
      if (name === "headersData") {
        if (this.headersData.length > 1) {
          this.headersData.splice(index, 1);
        }
      }

      if (name === "bodyFormData") {
        if (this.bodyFormData.length > 1) {
          this.bodyFormData.splice(index, 1);
        }
      }

      if (name === "bodyXFormData") {
        if (this.bodyXFormData.length > 1) {
          this.bodyXFormData.splice(index, 1);
        }
      }
    },
    handleNameBlur: function() {
      // alert(this.addForm.name);
      let name = this.addForm.name;
      if (name === undefined || name === "undefined" || name === "") {
        this.nameError = "请求方法不能为空";
      } else {
        this.nameError = "";
      }
    },
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    }
  },
  mounted() {
    this.loadHttpClientList();
  }
};
</script>

<style>
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  /* margin-left: 10px; */
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  /* margin-left: 10px; */
  vertical-align: bottom;
}
/* .el-loading-text{
  margin:3px -50px !important;
  width: 120px;
} */
.custom-loading {
  top: -110px;
  left: 50%;
}
.custom-loading .el-loading-spinner .el-loading-text {
  width: 120px;
  position: relative !important;
  top: 5px;
  right: 50px;
}
</style>


