<template>
  <section>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form ref="queryForm" :inline="true" :model="queryForm">
        <el-form-item label="搜索">
          <el-input
            size="medium"
            v-model="queryForm.keyword"
            placeholder="请输入关键字"
            style="width:300px"
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button size="medium" type="primary" @click="onSubmitQuery">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-button size="medium" type="primary" @click="handleAdd">新增</el-button>
    </el-col>

    <!-- 首页 表格控件 -->
    <el-table
      :data="hookData"
      v-loading="hookDataLoading"
      element-loading-text="数据加载中. . ."
      style="width: 100%"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="name" label="名称" width="220"></el-table-column>
      <el-table-column prop="typeAlias" label="类型"></el-table-column>

      <el-table-column prop="available" label="是否可用">
        <!-- @change="handleSwitch(scope.row)" width="180"-->
        <template slot-scope="scope">
          <!-- active-text="启用" inactive-text="停用" -->
          <el-switch v-model="scope.row.available"></el-switch>
        </template>
      </el-table-column>

      <el-table-column prop="status" label="状态">
        <!-- @change="handleSwitch(scope.row)" width="180"-->
        <template slot-scope="scope">
          <span v-if="scope.row.status==='run'" style="color:#6ac144">运行中</span>
          <span v-if="scope.row.status==='stop'" style="color:red">关闭</span>
        </template>
      </el-table-column>

      <el-table-column prop="configCount" label="配置项/个" width="120"></el-table-column>
      <el-table-column prop="total" label="运行次数"></el-table-column>
      <el-table-column prop="expire" label="存活时间"></el-table-column>
      <el-table-column prop="startTime" label="开始时间" width="200"></el-table-column>
      <el-table-column prop="endTime" label="结束时间" width="200"></el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="scope">
          <el-button-group>
            <!-- 运行 -->
            <el-button
              size="mini"
              type="success"
              plain
              v-if="scope.row.status==='stop'"
              @click="handleRun(scope.$index, scope.row)"
            >运行</el-button>

            <!-- 关闭 -->
            <el-button
              size="mini"
              type="danger"
              plain
              v-if="scope.row.status==='run'"
              @click="handleStop(scope.$index, scope.row)"
            >关闭</el-button>

            <!-- 编辑 -->
            <el-button size="mini" icon="el-icon-edit" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <!-- 删除 -->
            <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="handleDelete(scope.$index, scope.row)"
            ></el-button>
            <!-- <el-button type="primary" size="mini" @click="handleSynch(scope.$index, scope.row)">Send</el-button> -->
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title="新增"
      v-if="addFormVisible"
      v-loading="addFormLoading"
      element-loading-text="数据加载中. . ."
      :visible.sync="addFormVisible"
      @close="cancelAdd"
      :disabled="true"
      width="70%"
    >
      <span slot="title" class="dialog-footer">
        <span style="font-size:20px">{{dialogTitle}}</span>
        <span>{{dialogDesc}}</span>
      </span>

      <el-form ref="addForm" :model="addForm" label-width="100px">
        <el-form-item label="名称" required :error="nameError">
          <el-input
            v-model="addForm.name"
            placeholder="请输入Hook的名称"
            style="width:60%"
            :disabled="dialogTitle=='编辑'"
            @blur="handleVerifyBlur('hookName')"
          ></el-input>
        </el-form-item>

        <el-form-item label="日期类型" label-width="100px">
          <el-select v-model="addForm.dateType" placeholder="请选择日期类型" style="width:60%">
            <el-option label="不设置时间区间" value="-1"></el-option>
            <el-option label="yyyy-MM-dd HH:mm:ss" value="1"></el-option>
            <el-option label="HH:mm:ss" value="2"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="开始时间" label-width="100px">
          <!-- <el-col :span="8"> -->
          <el-date-picker
            type="date"
            placeholder="选择日期"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            v-model="addForm.startYmd"
            style="width: 28%;"
            :disabled="addForm.dateType=='-1' || addForm.dateType=='2'"
          ></el-date-picker>&nbsp;&nbsp; - &nbsp;&nbsp;
          <!-- </el-col> -->
          <!-- <el-col class="line" :span="1">&nbsp;&nbsp;&nbsp;-</el-col> -->
          <!-- <el-col :span="8"> -->
          <el-time-picker
            placeholder="选择时间"
            format="HH:mm:ss"
            value-format="HH:mm:ss"
            v-model="addForm.startHms"
            style="width: 28%;"
            :disabled="addForm.dateType=='-1'"
          ></el-time-picker>
          <!-- </el-col> -->
        </el-form-item>

        <el-form-item label="结束时间" label-width="100px">
          <!-- <el-col :span="9"> -->
          <el-date-picker
            type="date"
            placeholder="选择日期"
            v-model="addForm.endYmd"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            style="width: 28%;"
            :disabled="addForm.dateType=='-1' || addForm.dateType=='2'"
          ></el-date-picker>&nbsp;&nbsp; - &nbsp;&nbsp;
          <!-- </el-col> -->
          <!-- <el-col class="line" :span="1">&nbsp;&nbsp;&nbsp;-</el-col> -->
          <!-- <el-col :span="9"> -->
          <el-time-picker
            placeholder="选择时间"
            v-model="addForm.endHms"
            format="HH:mm:ss"
            value-format="HH:mm:ss"
            style="width: 28%;"
            :disabled="addForm.dateType=='-1'"
          ></el-time-picker>
          <!-- </el-col> -->
        </el-form-item>

        <el-form-item label="运行周期" required :error="lifeCycleError" label-width="100px">
          <el-select
            v-model="addForm.lifeCycle"
            placeholder="请选择周期"
            style="width:60%"
            @blur="handleVerifyBlur('lifeCycle')"
          >
            <el-option label="执行一次" value="1"></el-option>
            <el-option label="每天" value="2"></el-option>
            <el-option label="工作日(不包含双休)" value="3"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="存活周期" required :error="expireError" label-width="100px">
          <el-input
            type="number"
            v-model="addForm.expire"
            min="1"
            placeholder="请输入存活周期，单位：秒(s)，示例：1"
            style="width:60%"
            @blur="handleVerifyBlur('expire')"
          ></el-input>
        </el-form-item>

        <el-form-item label="运行次数" label-width="100px">
          <el-input
            type="number"
            v-model="addForm.total"
            min="1"
            placeholder="请输入运行次数"
            style="width:60%"
            :disabled="addForm.lifeCycle=='1'"
          ></el-input>
        </el-form-item>

        <el-form-item label="是否可用" label-width="100px">
          <el-switch v-model="addForm.available"></el-switch>
        </el-form-item>

        <el-form-item label="备注" label-width="100px">
          <el-input
            type="textarea"
            v-model="addForm.remark"
            placeholder="请输入备注..."
            rows="5"
            style="width:60%"
          ></el-input>
        </el-form-item>

        <el-form-item label="类型" label-width="100px">
          <el-select v-model="addForm.type" placeholder="请选择Hook类型" style="width:60%">
            <el-option label="执行任务" value="task"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="配置项">
          <el-transfer
            v-model="value"
            :data="transferData"
            class="custom-transfer1"
            filterable
            filter-placeholder="请输入http请求名称"
            :titles="['待选', '已选']"
            style="line-height: 0px"
          ></el-transfer>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false">取消</el-button>
        <el-button type="primary" @click.native="addFormSubmit" :loading="addFormLoading">保存</el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import {
  redisTaskSelect,
  hookSave,
  hookList,
  hookRun,
  hookStop,
  hookDelete
} from "../../api/api";

export default {
  data() {
    return {
      lifeCycleError: "",
      expireError: "",
      nameError: "",
      runBtnText: "运行",
      transferData: [],
      value: [],
      hookData: [],
      hookDataLoading: false,
      dialogTitle: "",
      dialogDesc: "",
      // startYmd: "",
      // startHms: "",
      addFormVisible: false,
      addFormLoading: false,
      addForm: {},
      queryForm: {
        keyword: ""
      }
    };
  },
  mounted() {
    this.loadHookList();
  },
  methods: {
    loadHookList: function() {
      let reqData = {
        keyword: this.queryForm.keyword
        // host: "47.95.12.18",
        // db: "0"
      };

      this.hookDataLoading = true;
      hookList(reqData).then(res => {
        this.hookDataLoading = false;
        if (res.code === 200) {
          this.hookData = res.data;
        }
      });
    },
    handleRunBtnText: function(index, row) {
      // alert();
      if (row.status === "run") {
        this.runBtnText = "运行";
      } else if (row.status === "stop") {
        this.runBtnText = "关闭";
      } else {
        this.runBtnText = "运行";
      }
    },
    // 打开新增窗口
    handleAdd: function() {
      this.dialogTitle = "新增";
      this.addFormVisible = true;

      let reqData = {
        keyword: "RA:TASK_GROUP_CONFIG"
      };
      redisTaskSelect(reqData).then(res => {
        if (res.code === 200) {
          let object = res.data;
          for (const key in object) {
            this.transferData.push({
              label: object[key],
              key: key
            });
          }
        }
      });
    },
    // 关闭窗口
    cancelAdd: function() {
      this.initData();
    },
    initData: function() {
      this.addForm = {};
      this.transferData = [];
      this.value = [];
    },
    addFormSubmit: function() {
      console.log(this.addForm);
      if (!this.addForm.name) {
        this.handleVerifyBlur("hookName");
        return false;
      }

      let typeAlias = "";
      if (this.addForm.type === "task") {
        typeAlias = "执行任务";
      }

      let startTime = this.addForm.startYmd + " " + this.addForm.startHms;

      let endTime = this.addForm.endYmd + " " + this.addForm.endHms;

      let reqData = {
        name: this.addForm.name,
        type: this.addForm.type,
        typeAlias: typeAlias,
        expire: this.addForm.expire,
        startTime: startTime,
        endTime: endTime,
        dateType: this.addForm.dateType,
        lifeCycle: this.addForm.lifeCycle,
        available: this.addForm.available,
        total: this.addForm.total,
        config: this.value,
        remark: this.addForm.remark
      };

      console.log(reqData);

      hookSave(reqData).then(res => {
        this.$notify({
          title: "系统提示",
          message: res.msg,
          type: res.code === 200 ? "success" : "error"
        });

        if (res.code === 200) {
          this.addFormVisible = false;
          this.initData();
          this.loadHookList();
        }
      });
    },
    onSubmitQuery: function() {
      this.loadHookList();
    },
    // 编辑
    handleEdit: function(index, row) {
      this.dialogTitle = "编辑";
      this.addFormVisible = true;

      this.addForm = {
        name: row.name,
        type: row.type,
        // typeAlias: typeAlias,
        expire: row.expire,
        startYmd: row.startYmd,
        startHms: row.startHms,
        endYmd: row.endYmd,
        endHms: row.endHms,
        dateType: row.dateType,
        lifeCycle: row.lifeCycle,
        available: row.available,
        total: row.total,
        remark: row.remark,
        host: row.host,
        db: row.db
      };

      redisTaskSelect({
        host: row.host,
        db: row.db,
        keyword: "RA:TASK_GROUP_CONFIG"
      }).then(res => {
        if (res.code === 200) {
          let object = res.data;
          for (const key in object) {
            this.transferData.push({
              label: object[key],
              key: key
            });
          }
        }
      });

      this.value = row.config;
    },
    handleRun: function(index, row) {
      hookRun(row).then(res => {
        // console.log(res);

        this.$notify({
          title: "系统提示",
          message: res.msg,
          type: res.code === 200 ? "success" : "error"
        });

        if (res.code === 200) {
          this.loadHookList();
        }
      });
    },
    handleStop: function(index, row) {
      hookStop(row).then(res => {
        this.$notify({
          title: "系统提示",
          message: res.msg,
          type: res.code === 200 ? "success" : "error"
        });

        if (res.code === 200) {
          this.loadHookList();
        }
      });
    },
    handleVerifyBlur: function(name) {
      if (name === "hookName") {
        this.nameError = !this.addForm.name ? "名称不能为空" : "";
      }
      if (name === "expire") {
        this.expireError = !this.addForm.expire ? "存活周期不能为空" : "";
      }
      //lifeCycle
      if (name === "lifeCycle") {
        this.lifeCycleError = !this.addForm.lifeCycle ? "生命周期不能为空" : "";
      }
    },
    handleDelete: function(index, row) {
      this.$confirm("确定要删除此记录吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          hookDelete(row.key).then(res => {
            this.$notify({
              title: "系统提示",
              message: res.msg,
              type: res.code === 200 ? "success" : "error"
            });

            if (res.code === 200) {
              this.loadHookList();
            }
          });
        })
        .catch(() => {});
    }
  }
};
</script>

<style>
.custom-transfer1 .el-transfer-panel__item.el-checkbox .el-checkbox__label {
  overflow: visible;
  font-size: 12px;
}
.custom-transfer1 .el-transfer-panel {
  width: 260px;
}
</style>


