  },
  created() {},
  methods: {
    // 过期时间显示加高亮
    formatterExpire: function (val) {
      return val === "永不过期"
        ? "<font color='#757575'>" + val + "</font>"
        : "<font color='red'>" + val + "</font>";
    },
    // 选择主机后加载DB索引
    handleHostChange: function (val) {
      // alert(val);
      console.log(val);
      this.redisDbLoading = true;

      var _host = val.split(":")[0];
      var _port = val.split(":")[1];

      this.filters.host = _host;
      this.filters.port = _port;

      selectRedisDb({ host: this.filters.host, port: this.filters.port }).then(
        (res) => {
          // console.log(res);
          this.redisDbLoading = false;
          if (res.code === 200) {
            this.redisDb = res.data;
          } else {
            this.$message({
              message: res.msg,
              type: "error",
            });
          }
        }
      );
      return;
    },
    // 选择主机后加载DB索引
    handleSyncHostChange: function (val) {
      // alert(val);
      console.log(val);
      this.redisDbLoading = true;

      var _host = val.split(":")[0];
      var _port = val.split(":")[1];

      this.synchForm.hostAndPort = val;
      this.synchForm.host = _host;
      this.synchForm.port = _port;

      selectRedisDb({
        host: this.synchForm.host,
        port: this.synchForm.port,
      }).then((res) => {
        // console.log(res);
        this.redisDbLoading = false;
        if (res.code === 200) {
          this.redisDb = res.data;
        } else {
          this.$message({
            message: res.msg,
            type: "error",
          });
        }
      });
      return;
    },
    // 查询缓存列表
    getRedisCaches: function (val) {
      console.log(this.filters);

      if (!this.filters.host) {
        this.$message({ message: "警告：主机不能为空哦！", type: "warning" });
        return;
      }
      if (!this.filters.db) {
        this.$message({ message: "警告：DB不能为空哦！", type: "warning" });
        return;
      }

      // 从第一页开始
      if (val == 1) {
        this.page = val;
      }

      let param = {
        pageNo: this.page,
        pageSize: this.pageSize,
        host: this.filters.host,
        port: this.filters.port,
        db: this.filters.db === "" ? "0" : this.filters.db,
        keyword: this.filters.keyword,
      };
      // console.log(param);

      // NProgress.start();
      this.listLoading = true;
      getRedisCacheList(param).then((res) => {
        // NProgress.done();
        // console.log(res);
        this.listLoading = false;
        if (res.code === 200) {
          this.cacheInfos = res.data;
          this.total = res.count;
        } else {
          this.cacheInfos = [];
          this.total = 0;
          this.$message({
            message: res.msg,
            type: "error",
          });
        }
      });
    },
    showDataType: function (val) {
      if (val === "zset") {
        this.scoreFlag = true;
        this.scoreFlag2 = true;
        this.addValueFlag = true;
        this.hkeyFlag = false;
      } else if (val === "hash") {
        this.addValueFlag = true;
        this.hkeyFlag2 = true;
        this.hkeyFlag = true;
        this.scoreFlag = false;
        this.scoreFlag2 = false;
      } else if (val === "list") {
        this.addValueFlag = true;
        this.editValueFlag = false;
      } else {
        this.scoreFlag = false;
        this.hkeyFlag = false;
      }
    },
    handleCacheKeywordInput(val) {
      this.cacheKeyword = val;
      this.elPage = 1;
      this.elPageSize = 10;
      let param = {
        keyword: this.editForm.key,
        cacheKeyword: val,
        type: this.editForm.dataType,
        host: this.filters.host,
        port: this.filters.port,
        db: this.filters.db,
        pageNo: this.elPage,
        pageSize: this.elPageSize,
      };
      this.pageRedisCacheDetails(param);
      // alert(val)
    },
    handleEditFormDataTypeChange(val) {},
    handleDataTypeChange(val) {
      // alert(val)

      this.showDataType(val);
    },
    // 详情页 每页条数
    handleRedisCacheDetailsSizeChange(val) {
      this.elPageSize = val;
      let param = {
        keyword: this.editForm.key,
        cacheKeyword: this.cacheKeyword,
        type: this.editForm.dataType,
        host: this.filters.host,
        port: this.filters.port,
        db: this.filters.db,
        pageNo: this.elPage,
        pageSize: this.elPageSize,
      };
      // 分页查询
      this.pageRedisCacheDetails(param);
    },
    handleSizeChange(val) {
      // alert('点击了设置每页条数')

      this.pageSize = val;
      this.getRedisCaches();
    },
    // 详情页 设置页码
    handleRedisCacheDetailsCurrentChange(val) {
      this.elPage = val;
      // alert(val)
      let param = {
        keyword: this.editForm.key,
        cacheKeyword: this.cacheKeyword,
        type: this.editForm.dataType,
        host: this.filters.host,
        db: this.filters.db,
        pageNo: this.elPage,
        pageSize: this.elPageSize,
      };
      // console.log(param)
      // return;

      // 分页查询
