        return;
      }
      this.rowObj = row;
      this.synchFormVisible = true;
    },
    //显示编辑界面
    handleEdit: function (index, row) {
      if (!this.filters.host) {
        this.$message({ message: "警告：主机不能为空哦！", type: "warning" });
        return;
      }
      if (!this.filters.db) {
        this.$message({ message: "警告：DB不能为空哦！", type: "warning" });
        return;
      }
      this.editFormVisible = true;
      this.hashValues = [];
