    // 更新名称
    editKeySubmit: function () {
      // alert("更新名称");
      this.editKeyLoading = true;
      let param = {
        dataType: this.editForm.dataType,
        oldRedisKey: this.editForm.oldRedisKey,
        redisKey: this.editForm.key,
        host: this.filters.host,
        port: this.filters.port,
