  mounted() {
    getRedisConfigSelect().then((res) => {
      if (res.code === 200) {
        res.data.forEach((item) => {
          item.hostAndPort = item.host + ":" + item.port;
          // console.log("item", item);
        });

        this.redisConfigSelect = res.data;
      }
    });
  },
};
</script>

<style>
.customWidth {
  width: 68%;
}
.customEditDialogWidth {
  width: 68%;
}
.el-tooltip__popper {
  max-width: 30%;
}
</style>


