<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-button type="primary" v-on:click="getUser">刷新</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSaveUser('add')"
            >新增</el-button
          >
        </el-form-item>
      </el-form>
    </el-col>

    <!--列表-->
    <template>
      <el-table
        :data="users"
        highlight-current-row
        v-loading="loading"
        style="width: 100%"
      >
        <el-table-column type="index" label="序号" width="80"></el-table-column>

        <!-- width="120" -->
        <el-table-column
          prop="nickname"
          label="昵称"
          sortable
        ></el-table-column>

        <!-- width="120" -->
        <el-table-column
          prop="username"
          label="账号"
          sortable
        ></el-table-column>
        <!-- width="120" -->
        <el-table-column
          prop="password"
          label="密码"
          sortable
        ></el-table-column>

        <!-- width="100" -->
        <!-- <el-table-column prop="sex" label="性别" sortable></el-table-column> -->

        <el-table-column prop="role" label="角色" sortable>
          <template slot-scope="scope">
            <span v-if="scope.row.role === 'ADMIN'">管理员</span>
            <span v-if="scope.row.role === 'DEV'">开发者</span>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <el-switch
              on-text="启用"
              off-text="停用"
              v-model="scope.row.status"
              @change="handleSwitch(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="birth" label="生日" width="120" sortable></el-table-column> -->
        <!-- <el-table-column prop="addr" label="地址" min-width="180" sortable ></el-table-column> -->

        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <!-- <el-button
              size="small"
              @click="handleSaveUser('edit', scope.$index, scope.row)"
              >编辑</el-button
            >
            <el-button
              type="danger"
              size="small"
              @click="handleDel(scope.$index, scope.row)"
              >删除</el-button
            > -->
            <el-button-group>
              <el-button
                size="mini"
                @click="handleSaveUser('edit', scope.$index, scope.row)"
                icon="el-icon-edit"
              ></el-button>
              <el-button
                type="danger"
                size="mini"
                @click="handleDel(scope.$index, scope.row)"
                icon="el-icon-delete"
              ></el-button
            ></el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </template>

    <el-dialog
      :title="formTitle"
      v-if="formVisible"
      :visible.sync="formVisible"
      v-model="formVisible"
      :close-on-click-modal="false"
    >
      <!-- :rules="formRules" -->
      <el-form
        :model="formArgs"
        :rules="formRules"
        label-width="80px"
        ref="formArgs"
        style="margin-right: 30px"
      >
        <el-form-item label="昵称" prop="nickname">
          <el-input
            v-model="formArgs.nickname"
            auto-complete="off"
            placeholder="请输入昵称"
          ></el-input>
        </el-form-item>
        <el-form-item label="账号" prop="username">
          <el-input
            v-model="formArgs.username"
            auto-complete="off"
            placeholder="请输入账号"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input
            v-model="formArgs.password"
            auto-complete="off"
            placeholder="请输入密码"
          ></el-input>
        </el-form-item>

        <!-- style="margin-left: 20px"  collapse-tags-->
        <el-form-item label="主机">
          <el-select
            style="width: 100%"
            v-model="formArgs.hosts"
            multiple
            placeholder="请选择"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="角色" prop="role">
          <el-radio-group v-model="formArgs.role">
            <el-radio label="ADMIN" v-model="formArgs.role">管理员</el-radio>
            <el-radio label="DEV" v-model="formArgs.role">开发者</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="状态" prop="status">
          <el-switch
            on-text="启用"
            off-text="停用"
            v-model="formArgs.status"
          ></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click.native="formVisible = false">取消</el-button>
        <el-button type="primary" @click.native="saveBtn" :loading="saveLoading"
          >保存</el-button
        >
      </div>
    </el-dialog>
  </section>
</template>
<script>
import {
  getUserList,
  getRedisConfigSelect,
  saveUser,
  deleteUser,
} from "../../api/api";
//import NProgress from 'nprogress'
export default {
  data() {
    return {
      filters: {
        name: "",
      },
      loading: false,
      users: [],
      // 新增和编辑弹框flag
      formVisible: false,
      formTitle: "新增",
      formArgs: {
        nickname: "",
        username: "",
        password: "",
        status: false,
        role: "DEV",
      },
      formRules: {
        nickname: [
          { required: true, message: "请输入昵称", trigger: "blur" },
        ],
        username: [{ required: true, message: "请输入账号", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
      saveLoading: false,
      options: [
        // {
        //   value: "127.0.0.1:6379",
        //   label: "本地-1",
        // },
        // {
        //   value: "127.0.0.2:6379",
        //   label: "本地-2",
        // },
        // {
        //   value: "127.0.0.3:6379",
        //   label: "本地-3",
        // },
        // {
        //   value: "127.0.0.4:6379",
        //   label: "本地-4",
        // },
        // {
        //   value: "127.0.0.5:6379",
        //   label: "本地-5",
        // },
        // {
        //   value: "127.0.0.6:6379",
        //   label: "本地-6",
        // },
        // {
        //   value: "127.0.0.7:6379",
        //   label: "本地-7",
        // },
        // {
        //   value: "127.0.0.8:6379",
        //   label: "本地-8",
        // },
        // {
        //   value: "127.0.0.9:6379",
        //   label: "本地-9",
        // },
        // {
        //   value: "127.0.0.10:6379",
        //   label: "本地-10",
        // },
        // {
        //   value: "127.0.0.11:6379",
        //   label: "本地-11",
        // },
      ],
      // selectValues: [],
      // value1: ["127.0.0.1:6379", "127.0.0.2:6379"],
    };
  },
  methods: {
    // 点击删除用户
    handleDel(index, row) {
      this.$confirm("确认删除该记录吗?", "提示", {
        type: "warning",
      })
        .then(() => {
          console.log("row", row);
          this.loading = true;

          deleteUser([row.id]).then((res) => {
            this.loading = false;
            this.getUser();
          });
        })
        .catch(() => {});
    },
    // 点击保存按钮 保存用户信息
    saveBtn() {
      // console.log(this.formArgs);
      // console.log(this.selectValues);
      var _reqdata = { ...this.formArgs };
      // console.log("reqdata", _reqdata);
      this.saveLoading = true;
      saveUser(_reqdata).then((res) => {
        this.saveLoading = false;
        this.$message({
          message: res.msg,
          type: res.code == 200 ? "success" : "error",
        });
        this.getUser();
        this.formVisible = false;
      });
    },
    //性别显示转换
    formatSex: function (row, column) {
      return row.sex == 1 ? "男" : row.sex == 0 ? "女" : "未知";
    },
    //获取用户列表
    getUser() {
      // let para = {
      // 	name: this.filters.name
      // };
      this.loading = true;
      //NProgress.start();
      getUserList().then((res) => {
        this.loading = false;

        if (res.code === 200) {
          this.users = res.data;
        }
        //NProgress.done();
      });
    },
    // 点击按钮弹出保存用户信息表单
    handleSaveUser(type, index, row) {
      console.log("type", type);
      console.log("index", index);
      console.log("row", row);

      this.formTitle = type == "add" ? "新增" : "编辑";
      this.formVisible = true;
      if ("edit" == type) {
        this.formArgs = row;
      } else {
        this.formArgs = {};
      }
    },
    // 加载redis配置信息
    loadRedisConfig() {
      getRedisConfigSelect().then((res) => {
        if (res.code === 200) {
          res.data.forEach((item) => {
            var _value = item.host + ":" + item.port;
            this.options.push({
              value: _value,
              label: item.name,
            });
          });
        }
      });
    },
  },
  mounted() {
    // 加载用户列表
    this.getUser();
    // 加载redis配置信息
    this.loadRedisConfig();
  },
};
</script>

<style scoped>
</style>


